.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  body {
    background: #222;
    font-family: "Comfortaa", sans-serif;
  }
  
  aside.context {
    text-align: center;
    color: #fff;
    line-height: 1.7;
  }
  aside.context a {
    text-decoration: none;
    color: #fff;
    padding: 3px 0;
    border-bottom: 1px dashed;
  }
  aside.context a:hover {
    border-bottom: 1px solid;
  }
  aside.context .explanation {
    max-width: 700px;
    margin: 6em auto 0;
  }
  
  footer {
    text-align: center;
    margin: 4em auto;
    width: 100%;
  }
  footer a {
    text-decoration: none;
    display: inline-block;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background: transparent;
    border: 1px dashed #fff;
    color: #fff;
    margin: 5px;
  }
  footer a:hover {
    background: rgba(255, 255, 255, 0.1);
  }
  footer a .icons {
    margin-top: 12px;
    display: inline-block;
    font-size: 20px;
  }
  
  .main-content {
    text-align: center;
    margin: 4em auto;
  }
  
/* Base Map Image Settings */

