/* Map panel placement settings */
  .map-side {
    height: 600px;
    width: 152px;
    position: absolute;
    transform-style: preserve-3d;
    transition: 0.3s ease;
  }
  .map-side .front,
  .map-side .back {
    width: 100%;
    height: 100%;
    position: absolute;
    background-repeat: no-repeat;
    background-position: left top;
    background-size: cover;
    background-image: var(--image);
    backface-visibility: hidden;
  }
  .map-side .back {
    background-image: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/back.png");
  }
  
  .map-side {
    top: 0;
  }
  .map-side.side-1 {
    left: 0;
    margin-left: 1.5px;
  }
  .map-side.side-2 {
    left: 50%;
    margin-left: -2px;
  }
  .map-side.side-3 {
    left: 0;
    margin-left: 3px;
  }
  .map-side.side-3 .back {
    transform: rotateY(180deg);
  }
  .map-side.side-4 {
    left: 50%;
    margin-left: -1px;
  }
  .map-side.side-4 .back {
    transform: rotateY(180deg);
  }
  .map-side.side-5 {
    left: 0;
  }
  .map-side.side-5 .back {
    background-image: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/1.png");
  }
  .map-side.side-6 {
    left: 50%;
  }
  .map-side.side-6 .front {
    background-size: 99.5%;
  }
  .map-side.side-6 .back {
    background-image: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/17.png");
  }
  
 /* Footstep and Scroll Name Settings */
  
  .scroll-name {
    position: absolute;
    bottom: 105px;
    left: -60px;
    width: 150px;
    height: 30px;
    font: 15px Satisfy, cursive;
    text-align: center;
    background: url("https://rawcdn.githack.com/aakashapoorv/Marauder-Map-in-React/38904ba2bd14605682c10e56bd306597fb334c22/public/img/scroll.svg") center center/cover;
    z-index: 10;
    color: #615349;
    cursor: default;
    opacity: 0;
  }
  .scroll-name p {
    display: inline-block;
    margin: 4px 0 0 15px;
  }

  .staircase {
    position: absolute;
    width: 6px;
    height: 12px;
    border-radius: 80% 80% 70% 70%/130% 130% 25% 25%;
    z-index: 10;
    opacity: 0;
  }

  .staircase:before {
    content: "";
    position: absolute;
    border-radius: 6px;
    width: 5px;
    height: 5px;
    top: 110%;
    left: 0px;
    border-radius: 0 0 100% 100%;
  }

  .footstep {
    position: absolute;
    background: #615349;
    width: 6px;
    height: 12px;
    border-radius: 80% 80% 70% 70%/130% 130% 25% 25%;
    z-index: 10;
    opacity: 0;
  }
  .footstep:before {
    content: "";
    position: absolute;
    border-radius: 6px;
    width: 5px;
    height: 5px;
    top: 110%;
    left: 0px;
    background: #615349;
    border-radius: 0 0 100% 100%;
  }
  .footstep.left {
    transform: rotate(5deg);
  }
  .footstep.right {
    transform: rotate(-3deg) translateY(15px) translateX(10px);
  }
  
  .footsteps-1 .footstep.left {
    bottom: 150px;
    left: 18px;
    transform: rotate(35deg);
