/* Fourth Floor Map Level Settings and Footstep Settings */

  .map-b1.active .side-1 {
    transform-origin: center left;
    transform: rotateY(180deg) skewY(2deg);
    transition: 0.5s all ease-in-out 0.6s;
  }
  .map-b1.active .side-1 .front {
    transform: rotateY(180deg);
  }
  .map-b1.active .side-2 {
    transform: rotateY(180deg) skewY(-2deg);
    transform-origin: center right;
    transition: 0.5s all ease-in-out 0.6s;
  }
  .map-b1.active .side-2 .front {
    transform: rotateY(180deg);
  }
  .map-b1.active .side-3 {
    left: -50%;
    transform: skewY(2deg) translateX(-100%);
    top: 8px;
    transition: 0.5s transform ease 0.8s, 0.3s left ease 0.8s, 0.5s top ease 0.8s;
  }
  .map-b1.active .side-4 {
    left: 100%;
    transform: skewY(-2deg) translateX(100%);
    top: 8px;
    margin-left: -7px;
    transition: 0.5s transform ease 0.8s, 0.3s left ease 0.8s, 0.5s top ease 0.8s,
      0.5s margin ease 0.8s;
  }
  .map-b1.active .side-5 {
    left: -100%;
    transform-origin: center left;
    transform: rotateY(180deg);
    transition: 0.5s transform, 0.7s left 0.8s, 0.2s margin 0.8s;
    top: 0px;
    margin-left: 4px;
  }
  .map-b1.active .side-5 .front {
    transform: rotateY(180deg);
    transition: 0.1s transform;
  }
  .map-b1.active .side-6 {
    left: 150%;
    transform: rotateY(180deg);
    transform-origin: center right;
    margin-left: -8px;
    transition: 0.5s transform 0.3s, 0.7s left 0.8s, 0.5s top 0.8s,
      0.5s margin 0.8s;
  }
  .map-b1.active .side-6 .front {
    transform: rotateY(180deg);
    transition: 0.1s transform;
  }
  .map-b1.active .footstep,
  .map-b1.active .scroll-name {
    opacity: 1;
    transition: 0.5s opacity 2.5s;
  }
  .map-b1.active .staircase {
    opacity: 1;
    transition: 0.5s opacity 2.5s;
  }
